@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  box-sizing: border-box;
  height: 100%;

  font-family: Lato, sans-serif;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background: rgb(73,221,216);
  background: -webkit-gradient(linear, left top, right bottom, color-stop(0%,rgba(73,221,216,1)), color-stop(100%,rgba(25,226,115,1)));
  background: -webkit-linear-gradient(-45deg,  rgba(73,221,216,1) 0%,rgba(25,226,115,1) 100%);
  background: -webkit-linear-gradient(315deg,  rgba(73,221,216,1) 0%,rgba(25,226,115,1) 100%);
  background: linear-gradient(135deg,  rgba(73,221,216,1) 0%,rgba(25,226,115,1) 100%);
}

#app .heading h1 {
  font-size: 10em;
  padding-top: 0.7em;
}

#app .details {
  width: 100%;
  margin: 0 auto;
  padding: 20px;
}

.panel-heading {
  background-color: black !important;
}

